<template>
	<div class="m-portlet m-portlet--brand">
		<div class="m-portlet__head">
			<div class="m-portlet__head-caption">
				<div class="m-portlet__head-title">
					<h3 class="m-portlet__head-text">
						Cấu hình tự động tăng cò theo mức điểm
					</h3>
				</div>
			</div>
			<div class="m-portlet__head-tools">
				<button @click="this.showTangCoForm" type="button" class="btn btn-brand m-btn m-btn--custom">Thêm cấu hình</button>
			</div>
		</div>
		<div class="m-portlet__body">
			<!--begin::Section-->
			<div class="m-section">
				<div class="m-section__content">
					<data-table 
						tableId="autoCommission_table"
						:header="this.tableHeader"
						:columns="this.tableColumns"
						:columnDefs="this.columnDefs"
						:columnSearch="this.columnSearch"
						:data="this.commission"
						:onEditRow="this.editAutoCommission"
						:onDeleteRow="this.deleteAutoCommission"
					/>
				</div>
			</div>
			<!--end::Section-->

			<div class="modal fade show" id="tangcoForm" tabindex="-1" role="dialog" aria-labelledby="modalTitle">
				<div class="modal-dialog" role="document">
					<div class="modal-content">
						<div class="modal-header">
							<h3 class="modal-title" id="modalTitle">Config tự động tăng cò</h3>
							<button type="button" class="close" data-dismiss="modal" aria-label="Close">
								<span aria-hidden="true">×</span>
							</button>
						</div>
						<div class="modal-body">
							<form class="m-form m-form--fit m-form--label-align-right m-form--group-seperator-dashed">
								<div class="form-group m-form__group row">
									<div class="col-lg-12">
										<span class="m-form__help">Áp dụng cho</span>
									</div>
									<div class="col-lg-4">
										<input disabled="true" type="text" class="form-control m-input" :value="this.user.code" />
									</div>
									<div class="col-lg-8">
										<input type="text" class="form-control m-input" v-model="subCode" />
									</div>
								</div>
								<game-info-selection :onlyToday="false" tab="" />
								<div class="form-group m-form__group row">
									<div class="col-lg-12">
										<span class="m-form__help">Nhập vào mức giới hạn</span>
										<input type="text" id="_limit_point" class="form-control m-input" placeholder="Mức giới hạn" />
									</div>
								</div>
								<div class="form-group m-form__group row">
									<div class="col-lg-12">
										<span class="m-form__help">Nhập mức tăng</span>
										<input type="text" id="incr" class="form-control m-input" placeholder="Mức tăng" />
									</div>
								</div>
							</form>
						</div>
						<div class="modal-footer">
							<button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
							<button @click="this.addAutoCommission" type="button" class="btn btn-primary">Tăng</button>
						</div>
					</div>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
	import moment from 'moment';
	import { mapGetters } from 'vuex';
	import DataTable from "../DataTable";
	import GameInfoSelection from './GameInfoSelection';

	export default {
		data(){
			let vm = this;

	        return {
	        	"tableHeader": ["Tài khoản","Miền","Đài","Kiểu","Mức giới hạn","Mức tăng"],
	        	"columnSearch": [{pos:0}, {pos:1}, {pos:2}, {pos:3}, {pos:4}, {pos:5}],
	        	"tableColumns": [{data: 'user_code'},{data: 'game_id'},{data: 'channel_id'},{data: 'prop_id'},{data: 'point'},{data: 'incr'}],
	        	"columnDefs": [
					{
						targets: 1,
						render: function(data, type, full, meta) {
							let g = vm.gameInfo.games[data];
							return g;
						},
					},
					{
						targets: 2,
						render: function(data, type, full, meta) {
							if(data == "") return "Tất cả";
							let channels = [];
							for(let i=0;i<data.length;i++){
								let channel = [];
								for(let j=0;j<data[i].length;j++){
									channel.push(vm.gameInfo.channels[data[i][j]].alias);
								}

								channels.push(channel.join(" - "))
							}
							
							return channels.join(", ");
						},
					},
					{
						targets: 3,
						render: function(data, type, full, meta) {
							if(data == "") return "Tất cả";
							let props = [];
							for(let i=0;i<data.length;i++){
								props.push(vm.gameInfo.props[data[i]]);
							}
							
							return props.join(", ");
						},
					}
				],

	            "today": moment().format('DD/MM/Y'),
	            "commission": [],
	            "subCode": "",
	            "selectedRecord": null
	        }
	    },

	    created(){
    		this.getIncrCommission();
    	},

    	components: {
        	'data-table': DataTable,
        	'game-info-selection': GameInfoSelection
        },

    	computed: {
			...mapGetters({
				user: 'user',
		  		gameInfo: 'gameInfo'
			})
		},

	    methods: {
	        getIncrCommission: function(){
	        	let vm = this;

	        	window.axios.get("admin-panel/tools/auto-commission")
	        				.then((res) => {
	        					res = res.data;
	        					vm.commission = res.data;
	        				})
	        				.catch((error) => {

	        				});
	        },

	        showTangCoForm: function() {
	        	let vm = this;

	        	$("#tangcoForm").on('show.bs.modal', function () {
	        		if(vm.selectedRecord){
	        			$('#gameSelection').val(vm.selectedRecord.game_id).trigger('change');
	        			$('#channelSelection').val(vm._generateSelectedChannel(vm.selectedRecord.channel_id)).trigger('change');
		        		$('#propSelection').val(vm.selectedRecord.prop_id).trigger('change');
		        		$('#_limit_point').val(vm.selectedRecord.point);
		        		$('#incr').val(vm.selectedRecord.incr);
		        		vm.subCode = vm.selectedRecord.user_code.replace(vm.user.code, "");
	        		}
				});

	        	$("#tangcoForm").on('hide.bs.modal', function () {
	        		vm.selectedRecord = null;
	        		$('#channelSelection').val(null).trigger('change');
	        		$('#propSelection').val(null).trigger('change');
	        		$('#gameSelection').val(null).trigger('change');
	        		$('#incr').val("");
	        		$('#_limit_point').val("");
	        		vm.subCode = "";
	        	});
	        	$("#tangcoForm").modal('show');
	        },

	        addAutoCommission: function(){
	        	let vm = this;

	        	let incr = $('#incr').val();
	        	let point = $('#_limit_point').val();
	        	let channel = $('#channelSelection').val();
        		let prop = $('#propSelection').val();
        		let game = $('#gameSelection').val();

        		window.axios.post("admin-panel/tools/auto-commission", {
        			game: game,
        			channel: channel.join(","),
        			prop: prop.join(","),
        			incr: incr,
        			point: point,
        			userCode: this.user.code + this.subCode
        		})
			    .then((response) => {
			    	if(response.data.code == 0){
			    		swal("", "Tăng cò thành công", "success");
			    		vm.getIncrCommission();
			    		$("#tangcoForm").modal('hide');
			    	}
			    })
			    .catch((error) => { });
	        },

	        editAutoCommission: function(id){
	        	let c = this.commission.filter((x) => { return x.id == id; });
	        	this.selectedRecord = c[0];
	        	this.showTangCoForm();
	        },

	        deleteAutoCommission: function(id){
	        	let vm = this;

	        	swal({"text": "Ban co chac chan muon xoa config nay?", "type": "warning", confirmButtonText: "Xoa"})
	        		.then((result) => {
	        			if(result.value){
	        				vm.deleteRow(id);
	        			}
	        		});
	        },

	        deleteRow: function(rId){
	        	let vm = this;
	        	
	        	window.axios.delete("admin-panel/tools/auto-commission/"+rId)
				    .then((response) => {
				    	if(response.data.code == 0){
				    		vm.getIncrCommission();
				    		$("#tangcoForm").modal('hide');
				    		swal("", "Xoa cấu hình thành công", "success");
				    	}
				    })
				    .catch((error) => { });
	        },

	        _generateSelectedChannel: function(channels){
	        	let cs = [];
				for(let i=0;i<channels.length;i++){
					let channel = [];
					for(let j=0;j<channels[i].length;j++){
						channel.push(channels[i][j]);
					}

					cs.push(channel.join("."))
				}
				
				return cs;
	        }
	    }
	}

</script>